import React, { memo, lazy, Suspense } from 'react'
import Loading from '../../common/Loading'
import LineTransition from '../../hocs/LineTransition'
import AppearTransition from '../../hocs/AppearTransition'

const hocs = {
  reveal: LineTransition,
}

const SvgItem = memo(({ position, data }) => {
  const SvgComponent = lazy(() => import(`../../svgs/${position}`))
  const x = `${data.x}px`
  const y = `${data.y}px`

  const { animation, order } = data

  if (!hocs[animation]) {
    const HOC = AppearTransition(SvgComponent)
    return (
      <Suspense fallback={<Loading />}>
        <HOC x={x} y={y} order={order} />
      </Suspense>
    )
  } else {
    const HOC = hocs[animation](SvgComponent)
    return (
      <Suspense fallback={<Loading />}>
        <HOC x={x} y={y} order={order} />
      </Suspense>
    )
  }
})

export default SvgItem
