import React, { memo } from 'react'
import styled from '@emotion/styled'
import CarSvg from '../../svgs/Car'
import { CITYWIDTH } from '../../constants/config'

const G = styled.g`
  opacity: 0;
  animation: car-${props => props.name} ${props => props.time}s linear infinite;
  animation-delay: ${props => props.delay}s;
  @keyframes car-${props => props.name} {
    from {
      opacity: 1;
      transform: translateX(${props => props.from}px);
    }
    to {
      opacity: 1;
      transform: translateX(${props => props.to}px);
    }
  }
`

const getRandomInt = (min, max) => {
  return Math.random() * (max - min) + min
}

const Car = memo(props => {
  const { speed, size, x, y } = props
  const data = Array(size)
    .fill(null)
    .map((u, i) => i)
  return (
    <g>
      {data.map(f => {
        const delay = getRandomInt(f * 5, (f + 1) * 5)
        const y2 = f % 2 ? y : y - 10
        return (
          <G
            key={`car-${f}`}
            name={f}
            to={CITYWIDTH}
            from={0}
            time={speed}
            delay={delay}
          >
            <CarSvg x={x} y={y2} />
          </G>
        )
      })}
    </g>
  )
})

export default Car
