import React from 'react'
import styled from '@emotion/styled'

import SvgItem from './SvgItem'
import ScenarioSvgItem from './ScenarioSvgItem'
import City from './City'
import Car from './Car'
import Road from './Road'
import useResponsive from '../../hooks/useResponsive'
import { WIDTH, SVGDIMENSIONS, SCENARIO_SPEEDS } from '../../constants/config'

const Container = styled.div`
  height: 100%;
  width: 100%;
  overflow: hidden;
`
const VizCanvas = ({ data, step, selectedScenario }) => {
  const [width, height] = useResponsive()

  const { city, car, road } = SCENARIO_SPEEDS.default
  return (
    <Container>
      <svg
        width={width - WIDTH}
        height={height}
        viewBox={`0 0 ${SVGDIMENSIONS.width} ${SVGDIMENSIONS.height}`}
      >
        <City x={198} y={265} speed={city} />
        <Road x={198} y={451} speed={road} />
        <Car x={198} y={451} speed={car} size={30} />

        {data.map(f => {
          const { page, id } = f
          const show = page <= step + 1 // step starts at 0 but page starts at 1
          if (show && selectedScenario === 'Default') {
            return <SvgItem key={`vis-${id}`} data={f} position={id} />
          } else if (selectedScenario !== 'Default') {
            return (
              <ScenarioSvgItem
                selectedScenario={selectedScenario}
                key={`vis-${id}`}
                data={f}
                position={id}
              />
            )
          } else {
            return null
          }
        })}
      </svg>
    </Container>
  )
}

export default VizCanvas
