import React from 'react'
import styled from '@emotion/styled'
// TODO: move brain.svg to a more suitable location once
// functionality has been worked out
import brain from './brain.svg'

const Card = styled.div``

const Icon = styled.img`
  height: 45px;
  width: 45px;
  border-radius: 5px;
  background-color: ${props => props.theme.green};
`

const Title = styled.h3`
  font-size: 0.875rem;
  margin: 4px 0 3px;
`

const Subtitle = styled.p`
  margin-top: 0;
  font-size: 0.875rem;
  font-weight: bold;
`

const Body = styled.p`
  font-size: 0.875rem;
  font-weight: 500;
`

const IconCard = ({ img = brain, title, subtitle, body }) => (
  <Card>
    <Icon src={img} />
    <Title>{title}</Title>
    <Subtitle>{subtitle}</Subtitle>
    <Body>{body}</Body>
  </Card>
)

export default IconCard
