import React, { useState } from 'react'
import styled from '@emotion/styled'

import SidePanel from '../SidePanel'
import Introduction from '../Introduction'
import VizCanvas from '../VizCanvas'

import useKeys from '../../hooks/useKeys'
import useFetch from '../../hooks/useFetch'
import { structure, contents, sections, WIDTH } from '../../constants/config'

const Container = styled.div`
  height: 100%;
  width: 100%;
  overflow: hidden;
`

const size = contents.length
const visOffset = structure['Introduction'].length

const Page = () => {
  const [data, loading] = useFetch('./data.json')
  const [step, setStep] = useState(0)

  const nextStep = () => setStep(f => (f < size ? f + 1 : f))
  const prevStep = () => setStep(f => (f > 0 ? f - 1 : f))
  const toStep = pos => setStep(f => pos)

  useKeys('right', nextStep, [size])
  useKeys('left', prevStep, [size])

  if (loading) {
    return <div>Loading...</div>
  } else {
    const chapter = contents[step]
    const selectedSection = sections[chapter]
    const offset = step - visOffset

    const selectedScenario =
      selectedSection !== 'Scenarios' ? 'Default' : chapter

    return (
      <Container width={WIDTH}>
        <SidePanel
          size={size}
          step={step}
          nextStep={nextStep}
          prevStep={prevStep}
          chapter={chapter}
          selectedSection={selectedSection}
          toStep={toStep}
        />
        {selectedSection === 'Introduction' && (
          <Introduction
            chapter={chapter}
            nextStep={nextStep}
            prevStep={prevStep}
          />
        )}
        {selectedSection !== 'Introduction' && (
          <VizCanvas
            data={data}
            step={offset}
            selectedScenario={selectedScenario}
          />
        )}
      </Container>
    )
  }
}

export default Page
